.GenHeader {
    background-color: rgba(100, 100, 100, 0.5);
    font-size: 34px;
    border-radius: 5px;
    padding: 5px 10px 5px 10px;
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    width: fit-content;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    font-weight: 700;
    border: 1.5px solid #070707;
    text-shadow: rgb(198, 198, 198) 1px 0 3px;
    z-index: 100;
    max-width: 90%;
}

.GenHeader span {
  font-size: 34px;
  font-weight: 700;
}