.Navbar {
    border-radius: 5px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background: rgb(11, 11, 11);
    background: linear-gradient(90deg, rgba(11, 11, 11, 1) 0%, rgba(31, 31, 31, 1) 82%, rgba(55, 55, 55, 1) 100%);
    border-bottom: 1.5px solid #070707;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    width: 100%;
    height: 36px;
}

.Navbar a {
    text-decoration: none;
    color: whitesmoke;
    font-size: large;
    font-weight: 500;
    z-index: 1000;
}

.Navbar a:hover {
    color: rgba(34, 255, 0, 0.3);
}

.Navbar ul {
    margin: 5px 5px 5px 0px;
    list-style: none;
    display: flex;
    flex-flow: row nowrap;
    z-index: 1000;
}

.Navbar li {
    padding: 0 5px;
}

.Navbar-logo {
    position: absolute;
    height: 100px;
    top: 5px;
    left: 5px;
    border-radius: 10px;
    border: 1.5px solid #070707;
}

.Hamburger div {
    display: none;
}

@media (max-width: 710px) {
    .App body {
        padding-top: 14px;
        margin-top: 36px;
    }
    
    .Navbar-logo {
        display: none;
    }

    .Navbar {
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 500;
    }
    
    .Navbar ul {
        flex-flow: column nowrap;
        background-color: rgba(255, 255, 255, 0.31);
        -webkit-backdrop-filter: blur(0.5rem);
        backdrop-filter: blur(0.75rem);
        position: fixed;
        top: -5px;
        right: -5px;
        height: 100vh;
        width: 150px;
        text-align: right;
        border-left: solid 1.5px #070707;
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
    }

    .Navbar-closed ul {
        transform: translateX(100%);
        transition: all 0.3s linear;
    }

    .Navbar-open ul {
        transform: translateX(0%);
        transition: all 0.3s linear;
    }

    .Navbar li {
        position: relative;
        left: -45px;
        width: inherit;
    }

    .Hamburger {
        z-index: 2000;
        width: 2rem;
        height: 2rem;
        position: fixed;
        top: 1px;
        right: 10px;
        display: flex;
        justify-content: space-around;
        flex-flow: column nowrap;
    }
    
    .Hamburger div {
        display: block;
        width: 2rem;
        height: 0.25rem;
        border-radius: 5px;
        transform-origin: 1px;
        transition: all 0.3s linear;
    }
    
    .Hamburger-closed {
        transform: translateX(0px);
        transition: all 0.3s linear;
    }
    
    .Hamburger-closed div {
        background-color: white;
    }
    
    .Hamburger-open {
        transform: translateX(-100px);
        transition: all 0.3s linear;
    }
    
    .Hamburger-open div {
        background-color: rgb(28, 28, 28);
    }
    
    .Hamburger-open div:nth-child(1) {
        transform: rotate(45deg);
    }
    
    .Hamburger-open div:nth-child(2) {
        transform: translateX(100%);
        opacity: 0;
    }
    
    .Hamburger-open div:nth-child(3) {
        transform: rotate(-45deg);
    }
}