.AboutStaff {
    background-color: rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    padding: 10px 10px 10px 10px;
    border-radius: 5px;
    background-size: cover;
    margin-left: 15vw;
    margin-right: 15vw;
    margin-bottom: 20px;
    border: 1.5px solid #070707;
    text-align: justify;
}

.about-them {
    background-color: transparent;
    -webkit-backdrop-filter: blur(0rem);
    backdrop-filter: blur(0rem);
    margin: 0;
    padding-top: 10px;
    min-height: 100px;
}

.profile-pic {
    width: 168px;
    height: 210px;
    border-radius: 50%;
    border: #212121 solid 4px;
    text-align: center;
    float: left;
    shape-outside: circle();
    margin: 0 20px 20px 0;
    z-index: 5000;
}

.name {
    font-size: 28px;
    font-weight: 1000;
    margin: 5px 0 0 0;
    padding: 0;
}

.role {
    font-size: 18px;
    font-weight: 700;
    margin: 5px 0 0 0;
    text-align: left;
}

.role span {
  font-size: 20px;
  font-weight: 700;
  margin: 5px 0 0 0;
  text-align: left;
}

@media (max-width: 500px) {
    .AboutStaff {
        text-align: center;
    }

    .profile-pic {
        width: 160px;
        height: 200px;
        float: none;
        margin-bottom: 0px;
    }

    .name {
      font-size: 24px;
    }

    .role {
      font-size: 15px;
      text-align: center;
    }

    .role span {
      font-size: 18px;
    }
}