.TextBox {
  background-color: rgba(255, 255, 255, 0.5);
  padding: 10px 10px 10px 10px;
  border-radius: 5px;
  background-size: cover;
  text-align: center;
  margin-left: 15vw;
  margin-right: 15vw;
  margin-bottom: 20px;
  border: 1.5px solid #070707;
  -webkit-backdrop-filter: blur(0.5rem);
  backdrop-filter: blur(0.75rem);
}

.TextBox ol {
  text-align: left;
}

.TextBox li {
  margin-right: 25px;
  padding-bottom: 5px;
}

.TextBox li:last-child {
  padding-bottom: 0;
}