.Countdown {
    background-color: rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    padding: 10px 10px 10px 10px;
    border-radius: 5px;
    background-size: cover;
    text-align: center;
    margin-left: 5vw;
    margin-right: 5vw;
    margin-bottom: 20px;
    border: 1.5px solid #070707;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.Countdown div {
    width: 25%;
    text-align: center;
    font-size: 20px;
}

.Countdown-values {
    font-size: 35px !important;
    color: #008015;
    font-weight: 700;
}