.GenButton {
    background-color: hsla(120, 100%, 25%, 0.5);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    padding: 15px 15px 15px 15px;
    border: 1.5px solid #070707;
    border-radius: 5px;
    font-size: 25px;
    color: white;
    font-weight: 800;
    margin-bottom: 10px;
    cursor: pointer;
  }

  .GenButton:hover {
    background-color: rgb(0, 92, 0);
  }