.Footer {
    border-radius: 5px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    background: linear-gradient(90deg, rgba(11, 11, 11, 1) 0%, rgba(31, 31, 31, 1) 82%, rgba(55, 55, 55, 1) 100%);
    border-top: 1.5px solid #070707;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
    margin-top: 20px;
}

.identi, .i3 {
    font-family: 'Josefin Sans', sans-serif;
    color: white;
    padding: 0;
    font-size: 24px;
    margin: 0px;
    font-weight: 500;
}

.i3 {
    color: green;
    font-size: 28px;
    font-weight: 600;
}

.Footer ul {
    padding: 0.4rem;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 0.4rem;
}

#twitter-button {
    margin-top: 3px;
}

#instagram-button {
    margin-top: 1px;
}