@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Manrope:wght@200;300;400;500;600;700;800&family=Oswald:wght@200;300;400;500;600;700&display=swap');

* {
  box-sizing: border-box;
  font-family: 'Manrope', sans-serif;
  color: #161616;
  font-weight: 400;
  background-color: rgb(24, 24, 24);
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

/* Scrollbar settings */
::-webkit-scrollbar {
  width: 10px;
  background-color: rgb(39, 39, 39);
}

::-webkit-scrollbar-track {
  background: rgb(30, 30, 30);
  border-radius: 100px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(1, 84, 1);
  background-image: linear-gradient(rgb(0, 52, 0), green);
  border-radius: 100px;
}

::-webkit-scrollbar-thumb:hover {
  background-image: linear-gradient(rgb(73, 73, 73), rgb(165, 165, 165));
}