.faq-drop-down {
    padding: 1%;
    background-color: hsla(120, 100%, 25%, 0.5);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    border: 1.5px solid #070707;
    border-radius: 5px;
    text-align: center;
    width: 90%;
    font-size: 18px;
    color: white;
    font-weight: 600;
    margin-bottom: 10px;
}

.faq-question {
    text-align: -webkit-center !important;
    text-align: center;
}

.option {
    background-color: rgb(209, 209, 209);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
}

@media (max-width: 500px) {
  .faq-drop-down {
    font-size: 16px;
  }
}